#compactSection1{width:100%;background:#000}#compactSection1 .container{width:100%;background:url(../../../../../../public/img/camera/compact/02_컴팩트카메라.jpg) no-repeat 50% 140%;background-size:cover;padding:190px 0 120px 0}#compactSection1 .container .gap{width:100%}#compactSection1 .container .gap .title{width:100%;text-align:center;margin:0 0 40px 0}#compactSection1 .container .gap .title h1{font-size:80px;font-weight:700;color:#fff}#compactSection1 .container .gap .content{width:100%}#compactSection1 .container .gap .content ul{width:100%;display:flex;justify-content:center}#compactSection1 .container .gap .content ul li{margin:0 20px;background:#fff;border-radius:12px}#compactSection1 .container .gap .content ul li:first-child{background-color:#5865f5}#compactSection1 .container .gap .content ul li:first-child span{color:#fff}#compactSection1 .container .gap .content ul li a{display:block;width:180px;height:180px;padding:30px 25px;text-align:center}#compactSection1 .container .gap .content ul li a figure img{transition:all .3s}#compactSection1 .container .gap .content ul li a span{font-size:16px;font-weight:700;color:#222;letter-spacing:-1px}#compactSection1 .container .gap .content ul li:hover figure img{transform:scale(1.1)}